
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
body {
  margin: 0;
}

.home-app {
    background-color: #f3f3f3;
    height: 105vh;
}
#calendar-icon {
    width: 35px;
    margin-right: 20px;
    vertical-align: middle;
    -webkit-box-shadow: 0 3px 10px 0 rgba(0, 0, 0, 0.2);
            box-shadow: 0 3px 10px 0 rgba(0, 0, 0, 0.2);
}
.date-location {
    width: 100%;
    height: 42px;
    font-family: 'Roboto', sans-serif;
    font-size: 28px;
    font-weight: 500;
    padding-top: 130px;
    text-align: center;
}
.location {
    font-size: 20px;
    margin-top: 10px;
    margin-left: 30px;
}
.main-logo-container {
    height: 500px;
}
.logo-home {
    margin-top: 100px;
    max-width: 100%;
    height: auto;
    width: 1100px;
}
.follow-us {
    width: 100%;
    height: 28px;
    font-family: 'Roboto', sans-serif;
    font-size: 23px;
    font-weight: bold;
    letter-spacing: 0.7px;
    text-align: center;
    color: #3391c2;
    margin-top: 100px;
}
.social-icons {
    width: 100%;
}
.icon {
    width: 45px;
    height: 45px;
    margin: 20px;
}
.shadow {
    -webkit-box-shadow: 0 3px 10px 0 rgba(0, 0, 0, 0.2);
            box-shadow: 0 3px 10px 0 rgba(0, 0, 0, 0.2);
}
.icon:hover {
  display: inline-block;
  position: relative;
  -webkit-animation: bounce 0.5s infinite linear;
  animation: bounce 0.5s infinite linear;
  color:000;
}
.icon:active {
    width: 50px;
    height: 50px;
}
@-webkit-keyframes bounce {
0% { top: 0;
}
50% { top: -0.8em;
}
70% { top: -0.4em;
}
100% { top: 0;
}
}
@keyframes bounce {
0% { top: 0;
}
50% { top: -0.8em;
}
70% { top: -0.4em;
}
100% { top: 0;
}
}

.navbar {
  width: 100%;
  height: 80px;
  border-radius: 15px;
  -webkit-box-shadow: 0 4px 10px 0 rgba(0, 0, 0, 0.16);
          box-shadow: 0 4px 10px 0 rgba(0, 0, 0, 0.16);
  position: fixed;
  z-index: 1000;
  background-color: #ffffff;
}
.navbar-left-logo {
  width: 140px;
  margin-left: 20px;
  margin-top: 25px;
  float: left;
}
.navbar-title {
  float: right;
  margin-top: 16pt;
  margin-right: 5vw;
  font-family: 'Montserrat';
  font-weight: bold;
}
.navbar-title:hover {
  font-weight: bold;
}
.navbar-title:active {
  color: #4c6c8b;
}
a:hover {
  cursor: pointer;
}

